.search {
    display: flex;
    width: 80%;
    align-items: center;
    margin-left: 10%;
    background-color: rgb(204, 199, 192);
    border-radius: 50px;
    padding: 5px;

}

.search input {
    background-color: rgb(204, 199, 192);
    border: none;
    width: 100%;

}

input:focus {
    outline: none;
}